import React from 'react';
import Logo from '../logo/logo.component';
import MobileNav from '../mobileNav/mobileNav.component';

const Header = () => {
  const [toggleNav, setToggleNav] = React.useState(false);
  return (
    <header className="mobile-header-container">
      <div className="mobile-header-top-bar">
        <Logo />
        <div
          onClick={() => setToggleNav(!toggleNav)}
          className={`hamburger ${toggleNav ? 'is-active' : ''}`}
        >
          <span className="line"></span>
          <span className="line"></span>
          <span className="line"></span>
        </div>
      </div>
      {toggleNav ? <MobileNav setToggleNav={setToggleNav} /> : null}
    </header>
  );
};

export default Header;
